"use client"

import React, { useState, useEffect, useCallback } from "react"
import { useSearchParams } from "next/navigation"
import { Card, CardHeader, CardContent, CardTitle, CardDescription, CardFooter } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import {
  FileUp,
  Loader,
  FileText,
  FileIcon,
  Download,
  RefreshCw,
  Upload,
  CheckCircle2,
  AlertCircle,
  Trash2,
  Clock,
  CheckCircle,
  XCircle,
  User,
  Users,
} from "lucide-react"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { Label } from "@/components/ui/label"
import { Badge } from "@/components/ui/badge"
import { Progress } from "@/components/ui/progress"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Separator } from "@/components/ui/separator"
import { cn } from "@/lib/utils"

interface Validation {
  validationStatus: "validated" | "rejected" | "pending"
  comment: string | null
  validatorName: string
}

interface SubmittedDocument {
  idPFE: number
  idLivrable: 1 | 2
  fichier: string
  validations: Validation[]
}

interface LivrableNames {
  [key: number]: string
}

export default function SoumettrePage() {
  const searchParams = useSearchParams()
  const idEtudiant = searchParams.get("idEtudiant") || "4"
  const idGroupe = searchParams.get("idGroupe") || "2"

  const [pdfFile, setPdfFile] = useState<File | null>(null)
  const [presentationFile, setPresentationFile] = useState<File | null>(null)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [submitSuccess, setSubmitSuccess] = useState(false)
  const [submitError, setSubmitError] = useState<string | null>(null)
  const [submittedDocuments, setSubmittedDocuments] = useState<SubmittedDocument[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [fetchError, setFetchError] = useState<string | null>(null)
  const [uploadProgress, setUploadProgress] = useState(0)
  const [activeTab, setActiveTab] = useState("submitted")

  const livrableNames: LivrableNames = {
    1: "Document PDF",
    2: "Présentation",
  }

  const livrableIcons: Record<number, React.ReactNode> = {
    1: <FileText className="h-5 w-5" />,
    2: <FileIcon className="h-5 w-5" />,
  }

const fetchDocuments = useCallback(async () => {
  setIsLoading(true);
  setFetchError(null);
  try {
    const response = await fetch(`http://localhost:5000/api/livrable/documents?idEtudiant=${idEtudiant}`);
    if (response.ok) {
      const data = await response.json();
      console.log("Documents fetched:", data); // Debug the response
      setSubmittedDocuments(data);
    } else {
      setFetchError("Impossible de récupérer les documents");
    }
  } catch (error) {
    setFetchError("Erreur lors de la récupération des documents");
    console.error("Fetch error:", error);
  } finally {
    setIsLoading(false);
  }
}, [idEtudiant]);

  useEffect(() => {
    fetchDocuments()
  }, [fetchDocuments])

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>, setFile: (file: File | null) => void) => {
    if (e.target.files?.[0]) {
      setFile(e.target.files[0])
      setUploadProgress(0)
      const timer = setInterval(() => {
        setUploadProgress((prev) => {
          if (prev >= 100) {
            clearInterval(timer)
            return 100
          }
          return prev + 10
        })
      }, 100)
    }
  }

  const resetForm = () => {
    setPdfFile(null)
    setPresentationFile(null)
    setUploadProgress(0)
    const pdfInput = document.getElementById("pdf") as HTMLInputElement
    const presentationInput = document.getElementById("presentation") as HTMLInputElement
    if (pdfInput) pdfInput.value = ""
    if (presentationInput) presentationInput.value = ""
  }

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    setSubmitSuccess(false)
    setSubmitError(null)
    setIsSubmitting(true)
    setUploadProgress(0)

    if (!pdfFile || !presentationFile) {
      setSubmitError("Veuillez sélectionner les deux fichiers.")
      setIsSubmitting(false)
      return
    }

    const formData = new FormData()
    formData.append("files", pdfFile)
    formData.append("files", presentationFile)
    formData.append("idEtudiant", idEtudiant)
    formData.append("idGroupe", idGroupe)
    formData.append("livrables", JSON.stringify([1, 2]))

    try {
      const response = await fetch("http://localhost:5000/api/livrable/submit-documents", {
        method: "POST",
        body: formData,
      })

      if (response.ok) {
        setSubmitSuccess(true)
        resetForm()
        fetchDocuments()
        setActiveTab("submitted")
      } else {
        const errorText = await response.text()
        setSubmitError(`Erreur lors de la soumission: ${errorText}`)
      }
    } catch (error) {
      console.error("Erreur de connexion:", error)
      setSubmitError("Erreur de connexion au serveur")
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleDelete = async (idPFE: number, idLivrable: number) => {
    if (!confirm("Êtes-vous sûr de vouloir supprimer ce document ?")) {
      return
    }

    try {
      const response = await fetch("http://localhost:5000/api/livrable/delete-document", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ idEtudiant, idPFE, idLivrable }),
      })

      if (response.ok) {
        fetchDocuments()
      } else {
        const errorText = await response.text()
        alert(`Erreur lors de la suppression : ${errorText}`)
      }
    } catch (error) {
      console.error("Erreur de connexion:", error)
      alert("Erreur de connexion au serveur")
    }
  }

  const getStatusIcon = (status: string) => {
    switch (status) {
      case "validated":
        return <CheckCircle className="h-4 w-4 text-green-500" />
      case "rejected":
        return <XCircle className="h-4 w-4 text-red-500" />
      default:
        return <Clock className="h-4 w-4 text-amber-500" />
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case "validated":
        return "Validé"
      case "rejected":
        return "Rejeté"
      default:
        return "En attente"
    }
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case "validated":
        return "bg-green-50 text-green-700 border-green-200 dark:bg-green-900/20 dark:text-green-400 dark:border-green-800"
      case "rejected":
        return "bg-red-50 text-red-700 border-red-200 dark:bg-red-900/20 dark:text-red-400 dark:border-red-800"
      default:
        return "bg-amber-50 text-amber-700 border-amber-200 dark:bg-amber-900/20 dark:text-amber-400 dark:border-amber-800"
    }
  }

  return (
    <div className="container mx-auto py-8 px-4">
      <Card className="w-full max-w-4xl mx-auto shadow-lg border-0 overflow-hidden bg-gradient-to-b from-background to-muted/20">
        <CardHeader className="pb-6 border-b border-border/40 bg-gradient-to-r from-primary/5 to-primary/10">
          <div className="flex items-center justify-between">
            <div>
              <CardTitle className="text-2xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-primary to-primary/80">
                Soumettre des documents
              </CardTitle>
              <CardDescription className="text-muted-foreground mt-1">
                Soumettez vos documents de projet pour évaluation
              </CardDescription>
            </div>
            <div className="flex items-center gap-2 bg-background/80 p-2 rounded-lg border border-border/40">
              <Users className="h-4 w-4 text-primary" />
              <span className="text-sm font-medium">Groupe: {idGroupe}</span>
              <Separator orientation="vertical" className="h-4 mx-1" />
              <User className="h-4 w-4 text-primary" />
              <span className="text-sm font-medium">ID: {idEtudiant}</span>
            </div>
          </div>
        </CardHeader>

        <CardContent className="p-0">
          {submitSuccess && (
            <Alert className="m-6 bg-green-50 dark:bg-green-900/20 text-green-800 dark:text-green-300 border-green-200 dark:border-green-800">
              <CheckCircle2 className="h-4 w-4" />
              <AlertTitle className="font-medium">Succès</AlertTitle>
              <AlertDescription>Documents soumis avec succès!</AlertDescription>
            </Alert>
          )}

          {submitError && (
            <Alert className="m-6 bg-destructive/10 text-destructive border-destructive/20">
              <AlertCircle className="h-4 w-4" />
              <AlertTitle className="font-medium">Erreur</AlertTitle>
              <AlertDescription>{submitError}</AlertDescription>
            </Alert>
          )}

          <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
            <div className="px-6 pt-6">
              <TabsList className="w-full grid grid-cols-2">
                <TabsTrigger value="submitted" className="text-sm">
                  <FileText className="h-4 w-4 mr-2" />
                  Documents Soumis
                </TabsTrigger>
                <TabsTrigger value="upload" className="text-sm">
                  <FileUp className="h-4 w-4 mr-2" />
                  Soumettre des Documents
                </TabsTrigger>
              </TabsList>
            </div>

            <TabsContent value="submitted" className="p-6 space-y-6">
              <div className="flex items-center justify-between">
                <h2 className="text-lg font-semibold flex items-center gap-2">
                  <FileText className="h-5 w-5 text-primary" />
                  Documents Soumis
                </h2>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={fetchDocuments}
                  disabled={isLoading}
                  className="h-8 hover:bg-primary/5 hover:text-primary"
                >
                  <RefreshCw className={`h-3.5 w-3.5 mr-1.5 ${isLoading ? "animate-spin" : ""}`} />
                  Actualiser
                </Button>
              </div>

              {isLoading ? (
                <div className="flex items-center justify-center p-12 bg-muted/10 rounded-xl border border-border/40">
                  <Loader className="h-6 w-6 animate-spin mr-3 text-primary" />
                  <p className="text-muted-foreground">Chargement des documents...</p>
                </div>
              ) : fetchError ? (
                <Alert variant="destructive" className="bg-destructive/10 text-destructive border-destructive/20">
                  <AlertCircle className="h-4 w-4" />
                  <AlertDescription>{fetchError}</AlertDescription>
                </Alert>
              ) : submittedDocuments.length > 0 ? (
                <div className="space-y-6">
                  {submittedDocuments.map((doc) => (
                    <Card
                      key={`${doc.idPFE}-${doc.idLivrable}`}
                      className="overflow-hidden border border-border/40 hover:shadow-md transition-all duration-300 hover:border-primary/20 group"
                    >
                      <div className="flex items-center justify-between p-4 bg-gradient-to-r from-muted/30 to-muted/10 border-b border-border/30">
                        <div className="flex items-center gap-3">
                          <div className="bg-primary/10 p-2.5 rounded-full group-hover:bg-primary/20 transition-colors">
                            {doc.idLivrable in livrableIcons &&
                              livrableIcons[doc.idLivrable as keyof typeof livrableIcons]}
                          </div>
                          <div>
                            <span className="font-medium">{livrableNames[doc.idLivrable]}</span>
                            <Badge variant="secondary" className="ml-2 text-xs">
                              Soumis
                            </Badge>
                          </div>
                        </div>
                        <div className="flex gap-2">
                          <Button
                            variant="ghost"
                            size="sm"
                            asChild
                            className="hover:bg-primary/5 hover:text-primary transition-colors"
                          >
                            <a href={`http://localhost:5000${doc.fichier}`} download>
                              <Download className="h-4 w-4 mr-2" />
                              Télécharger
                            </a>
                          </Button>
                          <Button
                            variant="ghost"
                            size="sm"
                            onClick={() => handleDelete(doc.idPFE, doc.idLivrable)}
                            className="text-red-500 hover:text-red-700 hover:bg-red-50 dark:hover:bg-red-900/20"
                          >
                            <Trash2 className="h-4 w-4" />
                          </Button>
                        </div>
                      </div>

                      {doc.validations.length > 0 ? (
                        <div className="p-5 space-y-4">
                          <div className="flex items-center gap-2">
                            <CheckCircle2 className="h-4 w-4 text-primary" />
                            <h3 className="text-sm font-medium">Validations</h3>
                          </div>
                          <div className="space-y-3">
                            {doc.validations.map((val, index) => (
                              <div
                                key={index}
                                className={cn(
                                  "text-sm p-4 rounded-lg border transition-all duration-200",
                                  getStatusColor(val.validationStatus),
                                )}
                              >
                                <div className="flex items-center gap-2 mb-1">
                                  {getStatusIcon(val.validationStatus)}
                                  <span className="font-medium">{val.validatorName}</span>
                                  <Badge
                                    variant="outline"
                                    className={cn(
                                      "ml-auto text-xs",
                                      val.validationStatus === "validated" && "border-green-200 dark:border-green-800",
                                      val.validationStatus === "rejected" && "border-red-200 dark:border-red-800",
                                      val.validationStatus === "pending" && "border-amber-200 dark:border-amber-800",
                                    )}
                                  >
                                    {getStatusText(val.validationStatus)}
                                  </Badge>
                                </div>
                                {val.comment && (
                                  <div className="mt-2 pl-6 border-l-2 border-muted py-1">
                                    <p className="text-sm">{val.comment}</p>
                                  </div>
                                )}
                              </div>
                            ))}
                          </div>
                        </div>
                      ) : (
                        <div className="p-5 flex items-center gap-2 text-sm text-muted-foreground">
                          <Clock className="h-4 w-4" />
                          Pas encore validé
                        </div>
                      )}
                    </Card>
                  ))}
                </div>
              ) : (
                <div className="flex flex-col items-center justify-center p-12 bg-muted/10 rounded-xl border border-border/40">
                  <Upload className="h-10 w-10 text-muted-foreground mb-3" />
                  <p className="text-muted-foreground text-center">Aucun document soumis pour le moment.</p>
                  <Button variant="link" onClick={() => setActiveTab("upload")} className="mt-2 text-primary">
                    Soumettre maintenant
                  </Button>
                </div>
              )}
            </TabsContent>

            <TabsContent value="upload" className="p-6 space-y-6">
              <div className="flex items-center gap-2 mb-4">
                <FileUp className="h-5 w-5 text-primary" />
                <h2 className="text-lg font-semibold">Soumettre de nouveaux documents</h2>
              </div>

              <form onSubmit={handleSubmit} className="space-y-6">
                {[
                  {
                    id: "pdf",
                    label: "Document PDF",
                    icon: <FileText />,
                    state: pdfFile,
                    setState: setPdfFile,
                    accept: ".pdf",
                    description: "Rapport de projet au format PDF",
                  },
                  {
                    id: "presentation",
                    label: "Présentation",
                    icon: <FileIcon />,
                    state: presentationFile,
                    setState: setPresentationFile,
                    accept: ".ppt,.pptx,.key,.pdf",
                    description: "Présentation du projet (PPT, PPTX, KEY ou PDF)",
                  },
                ].map((item) => (
                  <Card
                    key={item.id}
                    className={cn(
                      "overflow-hidden transition-all duration-300",
                      item.state ? "border-primary/30 shadow-sm" : "border-border/40",
                    )}
                  >
                    <CardHeader className="py-3 px-4 bg-gradient-to-r from-muted/30 to-muted/10 border-b border-border/30">
                      <Label htmlFor={item.id} className="flex items-center gap-2 font-medium cursor-pointer">
                        {React.cloneElement(item.icon, { className: "h-4 w-4 text-primary" })}
                        {item.label}
                      </Label>
                    </CardHeader>
                    <CardContent className="p-4">
                      <p className="text-xs text-muted-foreground mb-3">{item.description}</p>
                      <div className="relative">
                        <input
                          type="file"
                          id={item.id}
                          accept={item.accept}
                          onChange={(e) => handleFileChange(e, item.setState)}
                          className="block w-full text-sm text-muted-foreground
                            file:mr-4 file:py-2.5 file:px-4
                            file:rounded-lg file:border-0
                            file:text-sm file:font-medium
                            file:bg-primary file:text-primary-foreground
                            hover:file:bg-primary/90 cursor-pointer
                            border rounded-lg focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring"
                          required
                        />
                        {item.state && (
                          <>
                            <Badge className="absolute right-3 top-1/2 transform -translate-y-1/2 bg-primary/10 text-primary border-primary/20 px-2">
                              {item.state.name.length > 20 ? `${item.state.name.substring(0, 20)}...` : item.state.name}
                            </Badge>
                            <Progress value={uploadProgress} className="h-1 absolute bottom-0 left-0 right-0" />
                          </>
                        )}
                      </div>
                    </CardContent>
                  </Card>
                ))}

                <Button
                  type="submit"
                  className="w-full relative overflow-hidden group"
                  disabled={isSubmitting}
                  size="lg"
                >
                  <div
                    className="absolute inset-0 bg-gradient-to-r from-primary/0 via-primary/10 to-primary/0 
                    animate-shimmer -translate-x-full group-hover:translate-x-full transition-all duration-1000"
                  />
                  {isSubmitting ? (
                    <>
                      <Loader className="mr-2 h-5 w-5 animate-spin" />
                      Soumission en cours...
                    </>
                  ) : (
                    <>
                      <FileUp className="mr-2 h-5 w-5" />
                      Soumettre les documents
                    </>
                  )}
                </Button>
              </form>
            </TabsContent>
          </Tabs>
        </CardContent>

        <CardFooter className="p-4 border-t border-border/40 bg-gradient-to-r from-muted/30 to-muted/10 flex justify-between">
          <p className="text-xs text-muted-foreground">Système de soumission de documents PFE</p>
          <p className="text-xs text-muted-foreground">Dernière mise à jour: {new Date().toLocaleDateString()}</p>
        </CardFooter>
      </Card>
    </div>
  )
}

